import Vue from 'vue'
import VueRouter from 'vue-router'

import Makanan from '../components/Makanan.vue'
import Minuman from'../components/Minuman.vue'

Vue.use(VueRouter)

const routes = [

  {
    path: '/makanan',
    name: 'makanan',
    component: Makanan
},
{
    path:'/minuman',
    name:'minuman',
    component: Minuman
}
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
