<template>
<v-container> 
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data Dessert</h2> 
                <v-layout row wrap style="margin:10px"> 
                    <v-flex xs6> 
                        <v-btn
                        depressed 
                        dark 
                        rounded 
                        style="text-transform: none !important;" 
                        color = "green accent-3" 
                        @click="dialog = true"
                        >
                        <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah Dessert
                        </v-btn>
                    </v-flex>
                    
                </v-layout> 
            
                
                    
                        <v-row dense>
                        <v-col
                            v-for="(item) in desserts" 
                            :key="item.id"
                            :cols="4"
                        >
                        <v-card>
                            <v-card-title>{{ item.name }}</v-card-title>
                            <v-card-text class="text--primary">
                                <div>Carbs     :{{ item.carbs}}</div>
                                <div> Calories :{{ item.calories }}</div>
                                <div>Fat       :{{ item.fat }}</div>
                                <div>Protein   :{{item.protein}}</div>
                            </v-card-text>
                        
                            <v-card-actions>
                                 <v-btn icon class="mx-0" @click="editItem(item)">
                                <v-icon color="green">mdi-silverware</v-icon>
                              </v-btn >
                              <v-btn icon class="mx-0" @click="deleteItem(item)">
                                <v-icon color="red">mdi-delete</v-icon>
                              </v-btn>
                            </v-card-actions>
                            </v-card>
                        </v-col>
                    </v-row>
            </v-container> 
        </v-card> 
        <v-dialog v-model="dialog" max-width="500px">
<v-card>
  <v-card-title>
    <span class="headline">{{ formTitle }}</span>
  </v-card-title>
  <v-card-text>
    <v-container>
      <v-row>
        <v-col cols="12" sm="6" md="4">
          <v-text-field v-model="editedItem.name" label="Dessert name"></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="4">
          <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="4">
          <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="4">
          <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" md="4">
          <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
         </v-col>
        </v-row>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
      <v-btn color="blue darken-1" text @click="save">Save</v-btn>
    </v-card-actions>
</v-card>
</v-dialog>
    </v-container> 



</template>
<script>
    import {dessertsRef} from '../firebase';
    export default {
        data: () => ({
            cek : -1,
            dialog: false,
            desserts: [],
            editedIndex: -1,
            editedItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
            defaultItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
        }),
        firebase: {
            desserts : dessertsRef
        },
        computed: {
            formTitle () {
                return this.cek === -1 ? 'New Item' : 'Edit Item'
            },
        },
        watch: {
            dialog (val) {
                val || this.close()
            },
        },
        methods : {
            close() {
                    this.dialog = false
                },
                save() {
                    if(this.cek > -1){
                        dessertsRef.child(this.editedIndex).set({name: this.editedItem.name,
                        calories: this.editedItem.calories, fat: this.editedItem.fat,
                        carbs: this.editedItem.carbs, protein: this.editedItem.protein})
                        this.clear()
                    }else{
                        dessertsRef.push({
                            name: this.editedItem.name,calories: this.editedItem.calories, fat: this.editedItem.fat,
                            carbs: this.editedItem.carbs, protein: this.editedItem.protein
                        })
                        this.clear()
                    }
                    this.close()
                },
                clear(){
                    this.editedItem={}
                    this.editedItem.name = ''
                    this.editedItem.calories = 0
                    this.editedItem.fat = 0
                    this.editedItem.carbs = 0
                    this.editedItem.protein = 0
                    this.cek=-1
                },
                editItem(item){
                    this.editedIndex = item['.key']
                    this.cek = 0
                    this.editedItem = Object.assign({}, item)
                    this.dialog = true
                },
                deleteItem(item){
                    confirm('Are you sure you want to delete this item?') && dessertsRef.child((item['.key'])).remove()
                }   
          }
    }
</script>